import React, { useState } from "react";

import "../css/ProjectSection/project.css";
import { useSpring, animated } from "react-spring";
import { InView} from "react-intersection-observer";

const Project = ({ heading,link, tags, content, index ,imgPath,imgAlt}) => {
  const [inViewport, setInViewport] = useState(false);
  const springProp = useSpring({
    to: {
      opacity: inViewport ? 1 : 0,
      x: inViewport
        ? window.innerWidth - window.innerWidth * 0.85
        : 0,
    },
    from: {
      opacity: 0,
      x: inViewport
        ? window.innerWidth - window.innerWidth * (index % 2 === 0 ? 0.95 : 0.5)
        : 0,
    },
    delay: 800,
  });

  return (
    <InView
      onChange=/* {handleInViewChange} */ {(inView) => {
        setInViewport(inView);
      }}
    >
      <animated.div style={springProp}>
        <div className="projectContainer">
          {index % 2 === 0 ? (
            <div className="projectContentContainer">
              <div className="projectHeader">
                <div className="projectHeading">
                  <p><a href={link}>{heading}</a></p>
                </div>
                <div className="projectTagContainer">
                  {tags &&
                    tags.map((tag) => (
                      <aside className="projectTag">{tag}</aside>
                    ))}
                </div>
              </div>
              <article className="projectDescription">{content}</article>
            </div>
          ) : (
            <img
              className="projectImg"
              src="RapidGram.png"
              alt="RapidGram a personal social media site"
            />
          )}
          {index % 2 === 0 ? (
            <img
              className="projectImg"
              src={imgPath}
              alt={imgAlt}
            />
          ) : (
            <div className="projectContentContainer">
              <div className="projectHeader">
                <div className="projectHeading">
                  <p>{heading}</p>
                </div>
                <div className="projectTagContainer">
                  {tags &&
                    tags.map((tag) => (
                      <aside className="projectTag">{tag}</aside>
                    ))}
                </div>
              </div>
              <article className="projectDescription">{content}</article>
              <a>View on GitHub</a>
            </div>
          )}
        </div>
      </animated.div>
    </InView>
  );
};

export default Project;
