import React from "react";
import { Link } from "react-router-dom";

import "../../css/navbar.css";

const Navbar = ({ active }) => {
  console.log({ active });
  return (
    <nav className="navbar-nav">
      <ul className="navbar-ul">
        <li className="navbar-li">
          <span
            className={
              active === "davidsteenmann"
                ? "navbar-active nav-davidsteenmann"
                : ""
            }
          >
            <Link className={active === "projects" | active === "contact"?"link nav-black":"link"} to="/portfolioPage">
              David Steenmann
            </Link>
          </span>
        </li>
        <li className="navbar-li">
          <span className={active === "about" ? "navbar-active nav-about" : ""}>
            <Link className={active === "projects" | active === "contact"?"link nav-black":"link"} to="/about">
              About
            </Link>
          </span>
        </li>
        <li className="navbar-li">
          <span className={active === "skills" ? "navbar-active nav-skills" : ""}>
            <Link className={active === "projects" | active === "contact"?"link nav-black":"link"} to="/skills">
              Skills
            </Link>
          </span>
        </li>
        <li className="navbar-li">
          <span className={active === "projects" ? "navbar-active nav-projects" : ""}>
            <Link className={active === "projects" | active === "contact"?"link nav-black":"link"} to="/projects">
              Projects
            </Link>
          </span>
        </li>
        <li className="navbar-li">
          <span className={active === "contact" ? "navbar-active nav-contact" : ""}>
            <Link className={active === "projects" | active === "contact"?"link nav-black":"link"} to="/contact">
              Contact
            </Link>
          </span>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;
