import React from "react";
import Project from "../Project";
import Navbar from "./Navbar";

import { useSpring, useChain, animated } from "react-spring";
import "../../css/ProjectSection/projects.css";
import RapidGram from "../../img/rapidGram.png";

const Projects = () => {
  const headingProp = useSpring({from: {y: -10, opacity: 0},to:{y:0, opacity:1},delay: 400});

  return (
    <section className="projectSection">
      <Navbar active="projects"/>
      <animated.p style={headingProp} className="sectionHeading projectsHeading">Projects</animated.p>
      <Project
        heading="RapidGram - Fullstack"
        link="https://davidsteenmann.github.io/react-social-media-app/"
        tags={["JavaScript", "React", "Firebase"]}
        content={
          <p>
            The idea for RapidGram came to me after submitting my first
            university project "Songify", a music streaming website, created
            using PostgreSQL and PHP. <br />
            <br />
            Currently on RapidGram you can add pictures with captions, comment
            on them, like them and delete them if necessary. Furthermore, it is
            also possible to chat with each other via a global chat. RapidGram
            is currently under further development.
            <br />
            <br />
            The site was created using HTML,CSS, Javascript, JS React and
            Firebase. React allows me a dynamic creation of the website and also
            the integration of libraries and Apis is enormously facilitated by
            react. For the backend I used Firebase, because it keeps the backend
            fast and clear.
          </p>
        }
        index={0}
        imgPath={RapidGram}
        imgAlt="Rapid Gram a social media app"
      />
    </section>
  );
};

export default Projects;
